import React from "react"
import {
  DocumentsCollectionEventInfo,
  DocumentsCollectionRPJSONItem,
} from "@rp/react/tanstack-api/documents"
import { DocumentsCollectionTabBody } from "../DocumentsCollectionTabBody"

type DocumentsCollectionBodyProps = {
  visualItems: Array<DocumentsCollectionRPJSONItem> | null
  documentId: string
  tabIdx: number
  eventInfo: DocumentsCollectionEventInfo | null
  isFromChat: boolean
}

const DocumentsCollectionBody = ({
  visualItems,
  tabIdx,
  isFromChat,
}: DocumentsCollectionBodyProps) => {
  if (!visualItems?.length || !visualItems?.[tabIdx]) return null

  return visualItems.map((item, idx) => (
    <DocumentsCollectionTabBody
      key={item.info?.rpDocumentId}
      label={item.label}
      isVisible={tabIdx === idx}
      info={item.info}
      isFromChat={isFromChat}
    />
  ))
}

export { DocumentsCollectionBody }
